import { Component, OnInit, ViewChild, NgZone } from '@angular/core';
import { AlertController, ActionSheetController, ToastController, PopoverController } from '@ionic/angular';
import { MessageService } from '../../services/message.service';
import { Observable } from 'rxjs';
import { Router, ActivatedRoute } from '@angular/router';
import { UserService } from 'src/app/services/user.service';
import { CommonService } from 'src/app/services/common.service';
import { PopUpListComponent } from '../pop-up-list/pop-up-list.component';
import { PopUpUserComponent } from '../pop-up-user/pop-up-user.component';
import { ChatManager, TokenProvider } from '@pusher/chatkit-client';
import axios from 'axios';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-conversation',
  templateUrl: './conversation.page.html',
  styleUrls: ['./conversation.page.scss'],
})
export class ConversationPage implements OnInit {
  @ViewChild('content') private content: any;

  

  results: Observable<any>;
  userInfo: any = {};
  messages: any = {};
  chat: any = {
    message: '',
    user_id: '',
    room_id: '',
  };
  currentUser: any = {};
  conversations: any = [];
  isLoading: boolean = false;
  id: number = 0;

  isAssign: boolean = false;
  isCaseActioned: boolean = false;
  users: any;
  roomId: string;
  roomPusherId: string;
  chatCurrentUser: string;
  chatOtherUser: string;
  currentUserPusher: any;
  isUnread: any;
  terval: NodeJS.Timeout;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    public userService: UserService,
    public actionSheetController: ActionSheetController,
    public popoverController: PopoverController,
    public _zone: NgZone,
    public alertController: AlertController,
    public commonService: CommonService,
    public toastController: ToastController,
    private messageService: MessageService
  ) { }

  ngOnInit() {
    this.id = parseInt(this.route.snapshot.params['id'], 10);
    this.userInfo = this.userService.getUserInfo();
    this.chat.room_id = this.id;
    this.chat.user_id = this.userInfo.id;
  }

  ionViewWillEnter() {
    this.isLoading = true;
    this.getConversations();
    this.terval = setInterval(() => {
      this.checkIfIsRead();
    }, this.commonService.intervalTime);
  }

  ionViewWillLeave() {
    clearInterval(this.terval);
  }


  subscribeUser(currentUser: any, roomId: string) {
    currentUser.subscribeToRoom({
      roomId,
      messageLimit: 1,
      hooks: {
        onMessage: (message: any) => {
          console.log('message :', message);
          this.getConversations();
          // this.messages.push(message);
        },
        onPresenceChanged: (state: any, user: any) => {
          this.users = currentUser.users.sort((a: any, b: any) => {
            if (a.presence.state === 'online') return -1;
            return 1;
          });
        }
      }
    });

    this.currentUserPusher = currentUser;
    this.users = currentUser.users;
    this.roomId = roomId;
    console.log('this.currentUser :', this.currentUser);
    console.log('this.users :', this.users);
    console.log('this.roomId :', this.roomId);
  }
  
  addUser() {
    this.chatCurrentUser = 'cc_test1';
    this.chatOtherUser = 'cc_test2';

    axios
      .post('http://3.95.177.189/users', { username: this.chatCurrentUser })
      .then(() => {
        const tokenProvider = new TokenProvider({
          url: 'http://3.95.177.189/authenticate'
        });

        const chatManager = new ChatManager({
          instanceLocator: 'v1:us1:8bd77d3e-8eab-4be0-ad32-e4619f755209',
          userId: this.chatCurrentUser,
          tokenProvider
        });
        // origin room - 19430100
        return chatManager.connect().then((currentUser: any) => {
          if (this.roomPusherId) {
            this.subscribeUser(currentUser, this.roomPusherId);
          }
          else {
            currentUser.createRoom({
              name: 'cc_'+environment.env+'_'+this.chat.room_id,
              customData: { foo: 42 },
            }).then((room: any) => {
              console.log(`Created room called ${room.name}`, room);
              this.messageService.updatePusherRoomId({id: this.chat.room_id, room_id: room.id})
              .subscribe(() => {
                this.subscribeUser(currentUser, room.id);
              });
            })
            .catch((err: any) => {
              console.log(`Error creating room ${err}`)
            })
          }

        });
      })
      .catch(error => console.error(error));
  }


  async presentPopover(ev: any) {
    const popover = await this.popoverController.create({
      component: PopUpListComponent,
      event: ev,
      animated: true,
      showBackdrop: true,
      componentProps: {
        contacts: this.messages.contacts
      }
    });
    return await popover.present();
  }
  
  async showUserPopup(contact: any) {
    const popover = await this.popoverController.create({
      component: PopUpUserComponent,
      // event: ev,
      animated: true,
      showBackdrop: true,
      componentProps: {
        contact
      }
    });
    return await popover.present();
  }

  eventHandler(event: any) {
    if (event.keyCode === 13 && this.chat.message.trim()) {
      this.sendMessage();
    }
  }

  scrollToBottomOnInit() {
    this._zone.run(() => {
      setTimeout(() => {
        this.content.scrollToBottom(300);
      }, 300);
    });
    // this.content.scrollToBottom(300);
  }

  sendMessage() {
    if (!this.chat.message.trim()) return;
    
    const { message } = this.chat;
    this.currentUserPusher.sendMessage({
      text: message,
      roomId: this.roomId
    });

    // this.chat.message = '';
    // this.scrollToBottomOnInit();


    this.messageService.storeConversations(this.chat)
    .subscribe((res: any) => {
      this.conversations.push(res);
      this.chat.message = '';
      this.scrollToBottomOnInit();
    });
  }
  checkIfIsRead() {
    this.messageService.getCheckIfIsRead({id: this.id})
    .subscribe((res: any) => {
      this.isUnread = res.count;
    });
  }

  getConversations() {
    this.results = this.messageService.getConversations(this.id);

    this.results.subscribe(o => {
      this.isLoading = false;
      this.messages = o.room;
      this.currentUser = o.user;
      this.isUnread = o.isUnread;
      this.conversations = o.room.conversations;
      // localStorage.setItem("cases", JSON.stringify(o));
      this.scrollToBottomOnInit();

      this.checkIfIsRead();

      this.roomPusherId = o.room.rpush_id;
      if (!this.currentUserPusher) {
        this.addUser();
      }
    });
  }
  async presentToastWithOptions(message: string) {
    const toast = await this.toastController.create({
      message: message || 'Your settings have been saved.',
      showCloseButton: true,
      position: 'top',
      duration: 2000,
      closeButtonText: 'Close'
    });
    toast.present();
  }
  async presentAlertConfirm(message, isAssign) {
    const alert = await this.alertController.create({
      header: 'Confirm',
      message,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            // console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Okay',
          handler: () => {
            // console.log('Confirm Okay');
            this.presentToastWithOptions(`Action saved successfully.`);

            this.isAssign = isAssign;
            this.isCaseActioned = true;
          }
        }
      ]
    });
    await alert.present();
  }

  async presentConfirmDelete(message: string, data: any) {
    const alert = await this.alertController.create({
      header: 'Confirm',
      message,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'success',
          handler: (blah) => {
            // console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Confirm',
          handler: () => {
            data.message = '';
            this.messageService.deleteMessage(data.id)
            .subscribe((res: any) => {
              this.commonService.presentToastWithOptions('Message successfully deleted.');
            });
            // console.log('Confirm Okay');

          }
        }
      ]
    });
    await alert.present();
  }
  async presentConfirmDeleteAll(message: string) {
    const alert = await this.alertController.create({
      header: 'Confirm',
      message,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'success',
          handler: (blah) => {
            // console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Confirm',
          handler: () => {
            this.messageService.deleteAllMessages({room_id: this.id, user_id: this.userInfo.id})
            .subscribe((res: any) => {
              this.commonService.presentToastWithOptions('All messages successfully deleted.');
              this.getConversations();
            });
            // console.log('Confirm Okay');

          }
        }
      ]
    });
    await alert.present();
  }

  async presentActionSheet(message: any, isLastMessage: boolean) {
    const buttons = isLastMessage ? [{
      text: 'Delete Message',
      handler: () => {
        this.presentConfirmDelete('Are you sure you want to delete message?', message);
      }
    }, {
      text: 'Delete whole conversation',
      handler: () => {
        this.presentConfirmDeleteAll('Are you sure you want to delete whole conversation? This will only delete the whole conversation from your side.');
      }
    }, {
      text: 'Cancel',
      role: 'cancel',
      handler: () => {
        // console.log('Cancel clicked');
      }
    }] : [{
      text: 'Delete whole conversation',
      handler: () => {
        this.presentConfirmDeleteAll('Are you sure you want to delete whole conversation? This will only delete the whole conversation from your side.');
      }
    }, {
      text: 'Cancel',
      role: 'cancel',
      handler: () => {
        // console.log('Cancel clicked');
      }
    }];
    const actionSheet = await this.actionSheetController.create({
      header: 'Action',
      buttons
    });
    await actionSheet.present();
  }
  
  showOptions(message: any, index: number) {
    const isLastMessage = ((index + 1) === this.conversations.length);
    this.presentActionSheet(message, isLastMessage);
  }

}
